@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@350&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@350&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* MIC Logo */
img{
    left: 0;
    position: absolute;
    width: 10.4%;
    z-index: 1;
}

.nav-background{
    width: 100%;
    height: 100vh;
    background-color: #0077B6;
    background-image: linear-gradient(to bottom right,#03045E,#0077B6);
}

.nav-style{
    background: transparent;
    padding: 3%;
    padding-right: 0.5%;
}

.nav-item{
    font-family: 'Roboto Slab';
    font-size: 1.24rem;
    padding: 0 0.465rem;
    text-transform: uppercase; 
}

.navbar-collapse{
    padding-top: 0.28%;
}

.navbar-toggler{
    margin: 0.675rem;
    border: none;
}

@media screen and (max-width: 900px) {
    img{
        width: 15%;
    }
}

@media screen and (max-width: 450px) {
    img{
        width: 25%;
    }
}

.rightimg{
    width: 100%;
}
.right{
    align-items: center;
    justify-content: center;
    right: 0px;
    /*margin-top: 2px;*/
    width: 100%;
    position: absolute;
}

.left{
    left: 0px;
    position:relative;
    margin-left: 40px;
    margin-top: 90px;
    width: 100%;
    align-items: center;
    justify-content: center;
    
}

.home{
    justify-content: center;
    align-items: center;
    flex-direction: row;
    font-family: 'Roboto Slab';
    text-align: center;
    color: white;
}
.col{
    position: relative;
}
